@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;1,200;1,300;1,400&display=swap);
* {
    font-family: 'Nunito',
    sans-serif;


}

.coin-header {
  background: rgba(202, 202, 202, 0.22) !important;
  mix-blend-mode: multiply;
}

.currency_input_group {
  width:0 !important;
}

body {
  background-color: #fbfcff !important;
}


.nav-linker-anchor {
    font-size: 18px;
    font-weight:400 ;
}
.navbar {
    /* background: #CACACA; */
}

.sign-up {
    background: rgba(234, 233, 233, 0.29) !important;
    border-radius: 10px !important;
}

footer {
    background: #131111;
}

ul li,
ul li:hover {
    list-style-type: none;
    /* padding-bottom: 20px; */

}

ul li a,
ul li a:hover {
    color: white
}

ul h5 {
    color: white;
    margin-bottom: 20px;
    font-weight: 600
}

.about {
    line-height: 33px
}
.top-currency {
    color: rgba(19, 23, 32, 0.9);
    font-weight: 600;
    font-size: 50px;
}

.cur-card{
    background-color: rgb(255, 255, 255);
    box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 50px 0px;
    min-width: unset;
    width: calc(100% - 32px); 
    max-width: unset;
    margin-top: 24px;
    margin-bottom: 32px;
    margin-left: 0px;
    padding: 32px;
    z-index: unset;



    overflow: visible;
    position: relative;
    z-index: 0;
    display: flex;
    flex: 1 1;
    flex-direction: column;
    justify-content: space-between;
    margin: 16px;
    box-sizing: border-box;
    border-radius: 5px;
    background: rgb(255, 255, 255);
}

.details {
    display: grid;
    grid-template-columns: 32px 1fr 14px;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    font-weight: bold;
    font-size: 14px;
    color: rgb(19, 23, 32);
}

.world {
    background-color: rgba(10, 193, 142, 0.1);
    border-radius: 50%;
    width: 32px;
    height: 32px;
    position: relative;
}

.cur-name {
    max-width: 60px;
    align-self: center;
}
.empty {
    margin-left: 10px;
    margin-top: 0px;
}

.graph {
    transition: background-color 0.1s linear 0s,
    color 0.1s linear 0s;
    text-align: center;

}

.graph img {
    width: auto !important;
    height:60px;
}
#exchange-header {
    height:200px;
    text-align:center;
    background:#efc354;
}
.bg-wrapper {
     height:90px;
     align-items: flex-end;
     background:#e1dfe9;
     color:#0b216a;
     border-top-left-radius: 50px;
     border-top-right-radius: 50px;
}
.location {
    font-size: 12px;
    font-weight: 500;
}


.card-list {
    -webkit-column-count: 3;
            column-count: 3;
}

.card--item,
.autor-details {
    margin-bottom: 1rem;
    position: relative;
}

.card--item:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    border-radius: 7px;
    background: rgba(0, 0, 0, 0.5);
}

.card-image {
    flex: 100% 1;
    margin-top: 0.5rem;
    border-radius: 7px;
}

@media only screen and (max-width: 600px) {
    .card-list {
        -webkit-column-count: 1;
                column-count: 1;
    }
}

#container {
    padding-right: 50px;
    padding-left: 50px;
    margin-right: auto;
    margin-left: auto;
}

@media (min-width: 768px) {
    #container {
        width: 750px;
    }
}

@media (min-width: 992px) {
    #container {
        width: 970px;
    }
}

@media (min-width: 1200px) {
    #container {
        width: 1170px;
    }
}

.card--item img {
    width: 100%;
    height: auto;
    display: block;
    margin-bottom: 10px;
    border-radius: 5px;
}

.text-block {
    position: absolute;
    bottom: 5px;
    color: white;
    display: block;
    padding-left: 20px;
}

.blog-post-image {
    height: 400px;
    width: 100%;
    object-fit: cover;
    border-radius: 7px;
}
.autor-details:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    border-radius: 7px;
    background: rgba(0, 0, 0, 0.5);
}

